import { PageService } from './../../../page.service';
import { MessageService } from 'src/app/service/message.service';
import { UploadService } from './../../upload.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.scss'],
})
export class UploadComponent implements OnInit {
  minprice;
  maxprice;
  videoName: string;
  photoName: string;
  length: any = 0;
  priceRange;
  priceSugg;
  description;
  urlVideo;
  urlPhoto;
  Category;
  categories: string[];
  video: File | null;
  photo: File | null;
  time: number = 0;
  constructor(public service: UploadService,public messageService:PageService) {}

  ngOnInit(): void {
    this.categoryList();
  }

  onSelectVideo(e) {
    let getTime;
    const file = e.target.files && e.target.files[0];

    if (file) {
      const file_: FileList = e.target.files;
      // this.video = file_.item(0);
      console.log(this.video);
      var reader = new FileReader();
      reader.readAsDataURL(file);

      reader.onload = (event) => {
        console.log(file, file.type);
      this.video = file;

        this.urlVideo = (<FileReader>event.target).result;
        if (
          file.type == 'video/mp4' ||
          file.type == 'video/ogg' ||
          file.type == 'video/webm'
        ) {
          console.log(file);
          this.videoName = file.name;
          let videoElement: any = document.createElement('video');
          videoElement.src = event.target.result;
          console.log(videoElement.readyState, videoElement.duration);
          let self = this;
          setTimeout(function () {
            console.log(videoElement.readyState, videoElement.duration);

            if (videoElement.readyState === 4) {
              // console.log(videoElement.readyState,videoElement.duration)

              self.length = self.secondsToTime(videoElement.duration);
              self.time = videoElement.duration;
              self.estimate(self.Category, self.time);

              console.log(self.length);
            }
          }, 2000);
        }
      };
    }
  }
  onSelectPhoto(e) {
    const file = e.target.files && e.target.files[0];
    let getTime;
    // this.checkFileDuration(e);
    if (file) {
      var reader = new FileReader();
      reader.readAsDataURL(file);
      // if(file.type.indexOf('image')> -1){
      //   this.format = 'image';
      // } else if(file.type.indexOf('video')> -1){
      //   this.format = 'video';
      // }
      const file_: FileList = e.target.files;
      this.photo = file_.item(0);
      reader.onload = (event) => {
        console.log(event);
        this.urlPhoto = (<FileReader>event.target).result;
        this.photoName = file.name;
      };
    }
  }
  secondsToTime(in_seconds: any) {
    in_seconds = parseFloat(in_seconds.toFixed(2));
    let time = '';

    let hours: any = Math.floor(in_seconds / 3600);
    let minutes: any = Math.floor((in_seconds - hours * 3600) / 60);
    let seconds: any = in_seconds - hours * 3600 - minutes * 60;
    //seconds = Math.floor( seconds );
    seconds = seconds.toFixed(0);

    if (hours < 10) {
      hours = '0' + hours;
    }
    if (minutes < 10) {
      minutes = '0' + minutes;
    }
    if (seconds < 10) {
      seconds = '0' + seconds;
    }
    time = minutes + ':' + seconds;

    return time;
  }
  checkFileDuration(e) {
    let getTime;
    var file: any = e.target.files && e.target.files[0];
    var reader = new FileReader();
    reader.onload = function (e) {
      console.log(file);
      if (
        file.type == 'video/mp4' ||
        file.type == 'video/ogg' ||
        file.type == 'video/webm'
      ) {
        let videoElement: any = document.createElement('video');
        videoElement.src = e.target.result;
        var timer = setInterval(function () {
          if (videoElement.readyState === 4) {
            getTime = this.secondsToTime(videoElement.duration);

            console.log(getTime);
          }
        }, 500);
      }
    };
    if (file) {
      reader.readAsDataURL(file);
    } else {
      alert('nofile');
    }
  }
  categoryList() {
    this.service.category().subscribe((res) => {
      this.categories = res?.categories;
      this.Category = res?.categories[0];
    });
  }
  getPrice(e) {
    console.log(e);
    this.estimate(e, this.time);
  }
  estimate(category: string, time: number) {
    this.service.estimate(category, time).subscribe((res) => {
      this.priceRange = res.data[0] + '-' + res.data[1];
      this.minprice = res.data[0];
      this.maxprice = res.data[1]
    });
  }
  createCourse() {
    if(this.priceSugg>this.maxprice || this.priceSugg<this.minprice){
      this.messageService.e("The price entered is not specified in the price range")
    }
    else{
      const video = new FormData();
      video.append('file', this.video);
      video.append('name', this.videoName);
      video.append('description', this.description);
      video.append('thumbnail', this.photo);
      video.append('category', this.Category);
      video.append('price', this.priceSugg);

      this.service.createCourse(video).subscribe((res) => {
        console.log(res);
        this.videoName = '';
        this.description = '';
        this.video = null;
        this.photo = null;
        this.photoName = '';
        this.priceRange = 0;
        this.length = 0;
        this.priceSugg = 0;
      });
    }

  }
}
